<template>
    <!-- 新增船舶 -->
    <div class="add-ship">
        <navigate :title="navigateTitle" position="absolute"/>
        <div class="scroll-wrapper">
            <mu-form class="form" :model="form" label-position="left" label-width="100" ref="form">
                <mu-form-item class="p-15 border-1px-b" prop="name" label="船舶名称" :rules="shipNameRules">
                    <mu-text-field v-model="form.name" placeholder="请输入船舶名称" prop="name" solo/>
                </mu-form-item>
                <mu-form-item class="p-15 border-1px-b" prop="mmsi" label="MMSI" :rules="MMSIRules">
                    <mu-text-field v-model="form.mmsi" placeholder="请输入 MMSI" prop="mmsi" solo/>
                </mu-form-item>
                <mu-form-item class="p-15 border-1px-b" prop="length" label="船长" :rules="lengthRules">
                    <mu-text-field v-model="form.length" placeholder="请输入船长"
                                   suffix="米" prop="length" type="number"
                                   solo/>
                </mu-form-item>
                <mu-form-item class="p-15 border-1px-b" prop="width" label="船宽" :rules="widthRules">
                    <mu-text-field v-model="form.width" placeholder="请输入船宽"
                                   suffix="米" type="number" prop="width"
                                   solo/>
                </mu-form-item>
                <mu-form-item class="p-15 border-1px-b" prop="pureHeight" label="船高"
                              :rules="pureHeightRules">
                    <mu-text-field v-model="form.pureHeight" placeholder="请输入船高"
                                   suffix="米" type="number" prop="pureHeight"
                                   solo/>
                </mu-form-item>
                <mu-form-item class="p-15 border-1px-b" prop="draft" label="设计吃水" :rules="draftRules">
                    <mu-text-field v-model="form.draft" placeholder="请输入设计吃水"
                                   suffix="米" type="number" prop="draft"
                                   solo/>
                </mu-form-item>
                <mu-form-item class="p-15 border-1px-b" prop="shipType" label="船舶类型" :rules="shipTypeRules">
                    <mu-select v-model="form.shipType" solo>
                        <mu-option v-for="(option, index) in typeOptions" :key="option.id" :label="option.name"
                                   :value="option.id"/>
                    </mu-select>
                </mu-form-item>
                <mu-form-item class="p-15 border-1px-b" prop="default" label="设为默认船舶" label-width="90%">
                    <mu-switch v-model="form.default"></mu-switch>
                </mu-form-item>
                <mu-form-item>
                    <mu-button class="background-primary" @click="submit" v-loading="loading" color="#fff"
                               data-mu-loading-size="24"
                               :disabled="loading"
                               flat>提交
                    </mu-button>
                    <mu-button color="#999" @click="clear" flat>重置</mu-button>
                </mu-form-item>
            </mu-form>
        </div>
    </div>
    <!-- 新增船舶 -->
</template>

<script>
    import Navigate from "../../../base/navigate/navigate"
    import {queryTypeDictByFid} from "../../../api/search"
    import {saveOrUpdateIShip} from "../../../api/personal"
    import {isArray} from "../../../types/index"

    export default {
        name: "add-ship",
        data() {
            return {
                /***
                 * 表单验证
                 */
                shipNameRules: [
                    {validate: val => !!val, message: '必须填写船舶名称'},
                    {validate: val => val.length >= 2, message: '船舶名称长度大于或等于 2 '}
                ],
                MMSIRules: [
                    {validate: val => !!val, message: '必须填写 MMSI'},
                    {validate: val => /^\d{9}$/.test(val), message: '请填写正确的 MMSI'}
                ],
                lengthRules: [
                    {validate: val => !!val, message: '必须填写船长度'},
                    {validate: val => /^(-?\d+)(\.\d{1,2})?$/.test(val), message: '请精确到小数点后两位'}
                ],
                widthRules: [
                    {validate: val => !!val, message: '必须填写船宽度'},
                    {validate: val => /^(-?\d+)(\.\d{1,2})?$/.test(val), message: '请精确到小数点后两位'}
                ],
                draftRules: [
                    {validate: val => !!val, message: '必须填写设计吃水深度'},
                    {validate: val => /^(-?\d+)(\.\d{1,2})?$/.test(val), message: '请精确到小数点后两位'}
                ],
                pureHeightRules: [
                    {validate: val => !!val, message: '必须填写高度'},
                    {validate: val => /^(-?\d+)(\.\d{1,2})?$/.test(val), message: '请精确到小数点后两位'}
                ],
                shipTypeRules: [
                    {validate: val => !!val, message: '必须选择船舶类型'},
                    {validate: val => this.typeOptions.findIndex(item => item.id === val) !== -1, message: '请选择正确的船舶类型'}
                ],
                /***
                 * 表单内容：
                 *    name: 船舶名称
                 *    mmsi: mmsi 号码  -> 手机号码
                 *    length: 船长度  ->  单位（米）
                 *    width: 船宽度  ->  单位（米）
                 *    draft: 设计吃水  ->  单位（米）
                 *    pureHeight: 高度  ->  单位（米）
                 *    船舶类型: 船舶类型  ->  queryTypeDictByFid 获取
                 */
                form: {
                    name: '',
                    mmsi: '',
                    length: '',
                    width: '',
                    draft: '',
                    pureHeight: '',
                    shipType: '',
                    default: false
                },
                /***
                 * 下拉选项：
                 *      value:  值
                 *      name:  名称
                 */
                typeOptions: [],
                loading: false
            }
        },
        computed: {
            navigateTitle() {
                return this.$route.params.id ? '修改船舶' : '新增船舶'
            }
        },
        created() {
            this.queryTypeDictByFid()
        },
        activated() {
            if (this.$route.params.id) {
                this.form = this.$route.params
            } else {
                this.form = {
                    name: '',
                    mmsi: '',
                    length: '',
                    width: '',
                    draft: '',
                    pureHeight: '',
                    shipType: '',
                    default: false
                }
            }
        },
        methods: {
            queryTypeDictByFid() {
                queryTypeDictByFid('010').then(res => {
                    this.typeOptions = isArray(res)
                })
            },
            submit() {
                this.$refs.form.validate().then(result => {
                    if (result) {
                        this.loading = true
                        this.form.isDefault = this.form.default
                        saveOrUpdateIShip(this.form).then(res => {
                            this.loading = false

                            this.$router.replace('/about/data')

                            this.$emit('update')
                        }).catch(err => {
                            this.loading = false

                            this.$toast('保存失败')
                        })
                    }
                })
            },
            clear() {
                this.$refs.form.clear()

                this.form = {
                    name: '',
                    mmsi: '',
                    length: null,
                    width: null,
                    draft: null,
                    pureHeight: '',
                    shipType: '',
                    default: false
                }
            }
        },
        components: {
            Navigate
        }
    }
</script>

<style lang="stylus" scoped>
    .add-ship
        padding-top 46px
</style>



